<template>
  <div class="edit-baby-container">
    
    <div
      :style="{ 'background-image': babyInfo.avatar ? `url( ${ babyInfo.avatar } )` : '' }"
      :class="[
        'avatar-area',
        !babyInfo.avatar && babyInfo.sex === 0 ? 'avatar-boy-back' : '',
        !babyInfo.avatar && babyInfo.sex === 1 ? 'avatar-girl-back' : '',
        'f-c'
      ]"
    >
      <img v-if="babyInfo.avatar" class="avatar" :src="babyInfo.avatar" alt="">
      <img v-else-if="babyInfo.sex === 0" class="avatar" src="@/assets/images/baby_boy.png" alt="">
      <img v-else-if="babyInfo.sex === 1" class="avatar" src="@/assets/images/baby_girl.png" alt="">
      <van-uploader
        class="uploader-box"
        :max-count="1"
        :after-read="afterUploadAvatar"
      >
        <img class="photo-icon" src="@/assets/images/baby_photo.png" alt="">
      </van-uploader>
    </div>

    

    <div class="info-row info-row-special f-row-between-c">
      <p class="label">
        宝宝昵称
      </p>
      <div class="content f-row-between-c">
        <div class="info one-ellipsis f-row-end">
          <van-field
            input-align="right"
            clearable
            v-model="babyInfo.name"
            placeholder="输入宝宝昵称"
          />
        </div>
      </div>
    </div>

    <div class="info-row f-row-between-c">
      <p class="label">
        宝宝性别
      </p>
      <div
        @click="
          sexPicker.isShowSexPicker = true;
          datePicker.isShowDatePicker = false;
          relationPicker.isShowRelationPicker = false;
        "
        class="content f-row-between-c"
      >
        <div class="info one-ellipsis f-row-end">
          {{ sexPicker.options[ babyInfo.sex ] }}
        </div>
        <img class="more-icon" src="@/assets/images/my_more.png" alt="">
      </div>
    </div>

    <div class="info-row f-row-between-c">
      <p class="label">
        宝宝生日
      </p>
      <div
        @click="
          sexPicker.isShowSexPicker = false;
          datePicker.isShowDatePicker = true;
          relationPicker.isShowRelationPicker = false;
        "
        class="content f-row-between-c"
      >
        <div class="info one-ellipsis f-row-end">
          {{ babyInfo.birth }}
        </div>
        <img class="more-icon" src="@/assets/images/my_more.png" alt="">
      </div>
    </div>

    <div class="info-row f-row-between-c">
      <p class="label">
        我和宝宝的关系
      </p>
      <div
        @click="
          sexPicker.isShowSexPicker = false;
          datePicker.isShowDatePicker = false;
          relationPicker.isShowRelationPicker = true;
        "
        class="content f-row-between-c"
      >
        <div class="info one-ellipsis f-row-end">
          {{ babyInfo.relation }}
        </div>
        <img class="more-icon" src="@/assets/images/my_more.png" alt="">
      </div>
    </div>

    <div class="picker-area">
      <van-picker
        show-toolbar
        v-if="sexPicker.isShowSexPicker"
        :columns="sexPicker.options"
        @confirm="confirmSex"
        @cancel="sexPicker.isShowSexPicker = false"
      />
      <van-datetime-picker
        v-if="datePicker.isShowDatePicker"
        v-model="datePicker.chooseDate"
        type="date"
        :min-date="datePicker.minDate"
        :max-date="datePicker.maxDate"
        @confirm="confirmBirth"
        @cancel="datePicker.isShowDatePicker = false"
      />
      <van-picker
        show-toolbar
        v-if="relationPicker.isShowRelationPicker"
        :columns="relationPicker.options"
        @confirm="confirmRelation"
        @cancel="relationPicker.isShowRelationPicker = false"
      />
    </div>

    <van-button
      @click="$router.push( '/my/my-baby/list' )"
      v-if="!relationPicker.isShowRelationPicker && !datePicker.isShowDatePicker && !sexPicker.isShowSexPicker"
      class="button-finish"
      type="default"
      text="完成"
      round
      color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"
    />

  </div>
</template>

<script>
import Vue from 'vue';
import { Field, DatetimePicker, Picker, Button, Uploader } from 'vant';

Vue.use(Field).use(DatetimePicker).use(Picker).use(Button).use(Uploader);

export default {
  data() {
    return {
      babyInfo: {
        sex: 0, // 0: 男 1: 女
        name: '晨晨',
        birth: '2018-01-02',
        relation: '奶奶',
        avatar: ''
      },
      
      datePicker: {
        minDate: new Date(1970, 0, 1),
        maxDate: new Date(2025, 10, 1),
        chooseDate: new Date(),
        isShowDatePicker: false
      },

      relationPicker: {
        options: ['妈妈', '爸爸', '爷爷', '奶奶', '外公', '外婆'],
        isShowRelationPicker: false
      },

      sexPicker: {
        options: ['男', '女'],
        isShowSexPicker: false
      },

      imgList: []
    }
  },

  created() {
    document.title = '青豆家长';
  },

  methods: {
    confirmBirth(value) {
      let year = new Date( value ).getFullYear(),
          month = new Date( value ).getMonth() + 1,
          date = new Date( value ).getDate();
      this.babyInfo.birth = `${year}-${month}-${date}`;
      this.datePicker.isShowDatePicker = false;
    },

    confirmRelation(value, index) {
      this.babyInfo.relation = value;
      this.relationPicker.isShowRelationPicker = false;
    },

    confirmSex(value, index) {
      this.babyInfo.sex = index;
      this.sexPicker.isShowSexPicker = false;
    },

    afterUploadAvatar(file, detail) {
      this.babyInfo.avatar = file.content;
    }
  }
}
</script>

<style lang="scss" scoped>
.edit-baby-container {
  width: 100%;
  min-height: 100%;
  padding: 20px 17px 0;
  box-sizing: border-box;
  background-color: #fff;

  .avatar-area {
    margin: auto;
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
    border-radius: 50%;
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;

    .avatar {
      width: 53px;
      height: 44px;
    }
    .uploader-box {
      width: 24px;
      height: 24px;
      position: absolute;
      bottom: 0;
      right: 0;

      .photo-icon {
        width: 24px;
        height: 24px;
      }
    }
  }
  .avatar-girl-back {
    background: rgba(255,222,227,1);
  }
  .avatar-boy-back {
    background-color: #D0ECFC;
  }

  .info-row {
    width: 100%;
    height: 55px;
    border-bottom: rgba(238,238,238,1) solid 1px;

    .label {
      width: 120px;
      font-size: 15px;
      color: rgba(51,51,51,1);
    }
    .content {
      width: calc(100% - 130px);
      
      .info {
        width: calc(100% - 17px);
        font-size: 15px;
        color:rgba(102,102,102,1);
      }
      .more-icon {
        width: 7px;
        height: 10px;
      }
    }
  }

  .info-row-special {
    .content {
      .info {
        width: 100%;
      }
    }
  }

  .button-finish {
    width: calc(100% - 40px);
    height: 45px;
    position: absolute;
    bottom: 40px;
    left: 20px;
  }
}
</style>


